import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, MenuController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  myMode = 0;
  mySKU = '';
  myName = '';
  myMessage = '';
  myBar='';
  myLocs='';
  myID = 0;
  newLoc = '';
  repmode = false;

  constructor(public nav: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public menuCtrl: MenuController, private connect: Connect, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef, public barcodeScanner : BarcodeScanner) {
    this.myMessage = '';
    this.myID=0;
    this.newLoc='';
  }

  recalc()
  {
  }

  ionViewDidEnter()  {
     this.recalc();
  }

  addchar(a)
  { this.newLoc = "" + this.newLoc + a;
  }
 
  doScan() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.checkCode(barcodeData.text);
       }
    }, (err) => {
    });
  }


  scanLoc() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.updateLoc(barcodeData.text);
       }
    }, (err) => {
    });
  }


  fake()
  { this.checkCode('9300462346120');
  }

  checkCode(s)
  {
         let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();

        let url = 'spitem.php?code='+s;
console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.myMode = data.result;
             if (that.myMode > 0) {
                that.mySKU = data.SKU;
                that.myName = data.name;
		  that.myBar = data.barcode;
                that.myLocs = data.locs;
                that.myID = data.id;
                that.newLoc='';
             }
             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }




  updateLoc(s)
  {
         let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Updating..."
	    });
	    loader.present();

        let url = 'spsetloc.php?code='+s+'&id='+this.myID+'&repmode='+this.repmode;
console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.myMode = data.result;
             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }

 goBack()
 { this.myMode = 0;
 }

 enterLoc()
 { this.myMode = 2;
 }

 saveLoc()
 { this.updateLoc(this.newLoc);
 }

}



