import { Observable } from 'rxjs';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { Injectable } from '@angular/core';

@Injectable()
export class Connect {
    private server_url: string = 'http://theparcelpeople.com.au/catalogue/server/';

    constructor(public http: Http) {

    }

    public PostQuery(object: string, parameter: any) {
        return this.http.get(this.server_url + object + parameter).map(res => res.json());
    }

    public getList(object: string) {
        return this.http.get(this.server_url + object).map(res => res.json());
    }

    public logError(err) {
        console.error('Error: ' + err);
    }

    public getServerUrl() {
      return this.server_url;
    }
}



